import React, { Component } from 'react';

class Portfolio extends Component {
    render() {
        return (
            <React.Fragment>
                <h1>My Portfolio</h1>
                <div className="portfolio-items">
                    <div className="portfolio-item">
                        <h2>Slick</h2>
                        <p>A modern and responsive website made with html, css and javascript, using smooth scrolling.</p>
                        <p><a href="https://wibblefish21.github.io/Responsive-Site/" target="_blank">Click here to view</a></p> 
                        <a href="https://wibblefish21.github.io/Responsive-Site/" target="_blank"> <img src={require('./portfolio-images/slick.PNG')} alt="Slick" /></a>
                        <hr/>
                    </div>   
                    <div className="portfolio-item">
                        <h2>PHP Blog</h2>
                        <p>A simple blog made in PHP. Includes a login system.</p>
                        <p><a href="https://github.com/Wibblefish21/phpblog" target="_blank">Click here to view source code</a></p> 
                        <a href="https://github.com/Wibblefish21/phpblog" target="_blank"> <img src={require('./portfolio-images/blog1.png')} alt="PHP Blog" /></a>
                        <hr/>
                    </div>   
                    <div className="portfolio-item">
                        <h2>Correlate</h2>
                        <p>A modern and responsive landing page for a fictional app.</p>
                        <p><a href="https://wibblefish21.github.io/Correlate/" target="_blank">Click here to view</a></p> 
                        <a href="https://wibblefish21.github.io/Correlate/" target="_blank"> <img src={require('./portfolio-images/site2.PNG')} alt="Correlate" /></a>
                        <hr/>
                    </div>
                    <div className="portfolio-item">
                        <h2>Travel</h2>
                        <p>A responsive image slider for a fictional travel site.</p>
                        <p><a href="https://wibblefish21.github.io/travel/" target="_blank">Click here to view</a></p> 
                        <a href="https://wibblefish21.github.io/travel/" target="_blank"> <img src={require('./portfolio-images/travel.PNG')} alt="Travel" /></a>
                        <hr/>
                    </div>   
                    <div className="portfolio-item">
                        <h2>PHP Image Gallery</h2>
                        <p>A simple site built with PHP that allows users to upload, edit and delete images.</p>
                        <p><a href="https://github.com/Wibblefish21/PhP-Image-Gallery/tree/master" target="_blank">Click here to view source code</a></p> 
                        <a href="https://github.com/Wibblefish21/PhP-Image-Gallery/tree/master" target="_blank"> <img src={require('./portfolio-images/imggal.png')} alt="image gallery site" /></a>
                        <hr/>
                    </div>  
                </div>
            </React.Fragment>
        );
    }
}

export default Portfolio;