import React, { Component } from 'react';
import Nav from '../Nav.jsx';
import { Link } from 'react-router-dom'
class Contact extends Component {



    render() {
        return (
           
            <React.Fragment>
                <h1>Contact Me</h1>
                <p>Send me an email with some basic info about yourself and your site and I will get back to you as soon as possible.</p>
                <form action="https://formspree.io/sean.enzocat@gmail.com" method="POST">
                <label for="name">Full Name / Company Name</label>
                <input type="text" id="name" placeholder="Name" name="name"/>
                <label for="email">Email</label>
                <input type="email"name="_replyto" id="email" placeholder="Email Address" />
                <label for="message">Message</label>
                <textarea id="message" cols="30" rows="10" placeholder="Message" name="message"></textarea>
                <input type="submit" value="Submit" className="submit-btn"/>
                </form>
            </React.Fragment>
        );
    }
}

export default Contact;